import React from 'react'
import { SEO } from '../../components/SEO'
import Footer from '../../components/Footer'
import Header from '../../components/Header'
import Layout from '../../components/layout/Layout'
import { StaticImage } from 'gatsby-plugin-image'

const projectThree = () => {
  return (
    <Layout>
      <Header />
      <section class='mx-auto max-w-4xl dark:text-slate-400'>
        <div class='prose md:prose-lg max-w-none container mx-auto flex flex-col items-center justify-center px-5 py-8'>
          <div class='w-full text-left lg:w-5/6'>
            <h1 class='mb-6 text-3xl font-semibold leading-tight text-gray-900 dark:text-slate-400 sm:mb-10 sm:text-4xl'>
              Adaptive Quiz and Score-Based Branching | eLearning
            </h1>
          </div>
          <figure class='container mx-auto mb-6 flex w-5/6 flex-col items-center'>
            <StaticImage
              class='mb-1 rounded object-cover object-center'
              alt='Adaptive Quiz and Score-Based Branching'
              src='../../images/projects/adaptive-quiz-branching/Lead_Project_Image.jpg'
            />
            {/* <figcaption class='text-xs sm:text-sm'>
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Cum
              facilis perspiciatis, error nihil laborum necessitatibus tota.
            </figcaption> */}
          </figure>
          <div class='flex justify-center mb-6'>
            <button class='inline-flex rounded border-0 bg-orange-200 py-2 px-6 text-lg font-medium text-slate-700 hover:bg-orange-300 focus:outline-none shadow-md'>
              <a
                href='https://branching-adaptive-quiz-prototype.netlify.app/'
                target={'_blank'}
              >
                Explore Project
              </a>
            </button>
          </div>
          <div class='w-full text-left lg:w-5/6'>
            {/* <h2 class='title-font mb-4 text-3xl font-medium text-gray-900 sm:text-4xl'>
              Microdosing synth tattooed vexillologist
            </h2> */}
            <p class='mb-8 leading-relaxed'>
              This is a high-fidelity prototype of a quiz-based assessment which
              adapts the level of content shown to the learner based on the
              score they get. The project was initiated in response to a request
              from a client belonging to the immigration and study-abroad
              counselling industry.
            </p>
            <p class='mb-8 leading-relaxed'>
              Note that this project features placeholder content as the focus
              here is to showcase a working demonstration of how the programming
              features and score-based branching works.
            </p>
            <p class='prose mb-8 leading-relaxed dark:text-slate-400'>
              <ul className='list-disc list-outside'>
                <li>
                  <span className='font-bold'>Role:</span> eLearning Development
                </li>
                <li>
                  <span className='font-bold'>Target Audience:</span>{' '}
                  Immigration and study-abroad counselors
                </li>
                <li>
                  <span className='font-bold'>Tools Used:</span> Articulate
                  Storyline
                </li>
                <li>
                  <span className='font-bold'>Client:</span> An immigration and
                  study-abroad consulting firm (name not shared to respect the
                  client's non-disclosure agreement)
                </li>
              </ul>
            </p>
            <h2 class='title-font mb-4 text-2xl font-medium text-gray-900 dark:text-slate-400 sm:text-3xl'>
              Overview
            </h2>
            <p class='mb-8 leading-relaxed'>
              This concept came to life when a zonal head of an immigration and
              study-abroad consulting firm shared a tricky situation she was
              facing on the sales floor. The firm was in expansion phase and
              hiring numbers were up; the traditional training model they
              followed would not be able to keep up with the pace at which they
              needed the new joinees to hit the sales floor.
            </p>
            <p class='mb-8 leading-relaxed'>
              Considering that not all the new joinees were absolute freshers,
              there was a need to filter out the ones who didn't require a
              full-blown traditional classroom training as they were already
              abreast with the concepts needed to counsel their clients. This
              would accelerate the revenue generation process in line with the
              expansion.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Title Screen: Adaptive Quiz with Score-Based Branching'
                src='../../images/projects/adaptive-quiz-branching/02-Title Screen.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Title Screen: Adaptive Quiz with Score-Based Branching
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              This was a perfect scenario to demonstrate how an adaptive quiz
              would come to their rescue. The idea was to make the joinees take
              the quiz to decide whether they would be needing the full 30-day
              classroom orientation or they were fit for the sales floor. The
              score on that quiz would decide this branching.
            </p>
            <h2 class='title-font mb-4 text-2xl font-medium text-gray-900 dark:text-slate-400 sm:text-3xl'>
              Process
            </h2>
            <p class='mb-8 leading-relaxed'>
              The key elements for this concept were the scoring patterns and
              the passing criteria. Next, deciding the question type was also
              imperative.
            </p>
            <p class='mb-8 leading-relaxed'>
              I chose the most common question type: multiple choice questions;
              however, I decided to give multiple score weights to each of the
              three options. The best option carried maximum weight and the
              worst option minimum weight.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Question Type and Rules of the Quiz'
                src='../../images/projects/adaptive-quiz-branching/03-Scoring Criteria.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Question Type and Rules of the Quiz
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              Then came the passing criteria. I decided that 3 categories would
              work best for the prototype. If a learner scores 80% or more, they
              would not have to take any classroom or online refresher course,
              as they would be ready to join the sales floor. If a learner
              scores between 50% to 79%, they would need to take a short online
              refresher course to sharpen their blades. Lastly, if the learner
              scores any less than 50%, they were ideal candidates for a
              full-blown classroom training.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Branching Learners Using the Quiz Scoring Criteria'
                src='../../images/projects/adaptive-quiz-branching/04-Scoring Criteria.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Branching Learners Using the Quiz Scoring Criteria
              </figcaption>
            </figure>
            <h2 class='title-font mb-4 text-2xl font-medium text-gray-900 dark:text-slate-400 sm:text-3xl'>
              Developing the eLearning package in Storyline
            </h2>
            <p class='mb-8 leading-relaxed'>
              As far as the appearance and layout is concerned, I decided to go
              with an existing template in Articulate Storyline as the focus was
              to show the functional aspect of this assessment.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='MCQ Question Type with Weighted Scores'
                src='../../images/projects/adaptive-quiz-branching/08-MCQ Question Type.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                MCQ Question Type with Weighted Scores
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              There were two important design decisions here: how to show the
              rules of the quiz clearly and how to develop quiz results/feedback
              screen that implemented those rules.
            </p>
            <p class='mb-8 leading-relaxed'>
              Advanced triggers and system variables were used to store maximum
              score and the evaluated score after every question. This way we
              could transparently show the learners how much was the
              score/percentage and how much they were able to score.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Results Screen of a Learner that Passed the Quiz'
                src='../../images/projects/adaptive-quiz-branching/06-Quiz Success Results Screen.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Results Screen of a Learner that Passed the Quiz
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              Next, the above calculated scores were used to govern the
              branching to categorize learners who were ready, who needed a
              quick refresher and who required a full 30-day training. This was
              made possible by conditional triggers and using separate scenes
              for each possibility.
            </p>
            <p class='mb-8 leading-relaxed'>
              Even though the slide content was not the most important part, it
              had to be conversational and playful.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Results Screen Showing Success of the Learner'
                src='../../images/projects/adaptive-quiz-branching/07-Quiz Success Branch.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Results Screen Showing Success of the Learner
              </figcaption>
            </figure>
            <h2 class='title-font mb-4 text-2xl font-medium text-gray-900 dark:text-slate-400 sm:text-3xl'>
              Results and Takeaways
            </h2>
            <p class='mb-8 leading-relaxed'>
              The quiz prototype received positive feedback from the zonal
              manager.
            </p>
            <p class='mb-8 leading-relaxed'>
              On a personal level, this project helped me gain a better
              understanding of how eLearning done right can have tangible
              benefits. In this case, the concept was developed to help filter
              those who were ready to join the sales team without any classroom
              intervention. This meant faster revenue generation cycles.
            </p>
          </div>
        </div>
      </section>
      <Footer />
    </Layout>
  )
}

export default projectThree

export const Head = () => (
  <SEO title='Ankeet Patel - Adaptive Branching Quiz Prototype' />
)
